<template>
    <div>
        <el-header>
            <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
                <el-menu-item v-loading="this.name===''" style="float: right;font-weight: bold;">{{this.name}}CCN</el-menu-item>
            </el-menu>
        </el-header>
        <el-container>
            <el-aside width="200px">

            </el-aside>
            <el-main>
                <div class="demo-input-suffix" style="font-size: 30px;" >
            {{ this.hwName }}
        </div>
        <!-- <div  style="margin-top: 10px;font-size: 20px;">
            
        </div> -->
        <el-divider></el-divider>   
        <div> 
            <div v-html="compiledMarkdown">
            </div>
        </div>
        <input
            type="file"
            name="filename"
            id="open"
            style="display: none"
            @change="changeFile"
          />
        <el-main v-loading="upload">
            <div>
                
                <mavon-editor ref=md :toolbars="markdownOption" @imgAdd="$imgAdd" v-model="hwWrite">
                    <template slot="left-toolbar-after">
                        <button
                        type="button"
                        @click="$fileAdd"
                        class="op-icon el-icon-paperclip"
                        aria-hidden="true"
                        title=""
                        ></button>
                    </template>
                </mavon-editor>
            </div>
        </el-main>
        
        <div style="float: right;margin-right: 100px;">
            <el-button @click="save"></el-button>
            <el-button type="primary" @click="submit"></el-button>
            <!-- <el-button type="primary" @click="drawer = true"></el-button> -->
        </div>
        <div class="block">
  </div>
            </el-main>
        </el-container>
        
    </div>
</template>

<script>
    import axios from 'axios'
    import global from './GlobalPage.vue'
    const restweburl = global.ip;

    import { marked } from "marked";
    import hljs from "highlight.js";
    import "highlight.js/styles/monokai-sublime.css";

    export default {
        
      data() {
        return {
            hwName:'',
            hwContent:'loading...',
            hwWrite:" ",
            
            markdown: "# loading...",
            html: "",

            name:'',

            upload:false,

            markdownOption:{
                bold: true, // 
                italic: true, // 
                header: true, // 
                underline: true, // 
                strikethrough: true, // 
                mark: true, // 
                superscript: true, // 
                subscript: true, // 
                quote: true, // 
                ol: true, // 
                ul: true, // 
                link: true, // 
                imagelink: true, // 
                code: true, // code
                table: true, // 
                // fullscreen: true, // 
                // readmodel: true, // 
                // htmlcode: true, // html
                help: true, // 
                /* 1.3.5 */
                // undo: true, // 
                // redo: true, // 
                // trash: true, // 
                // save: true, // eventssave
                /* 1.4.2 */
                navigation: true, // 
                /* 2.1.8 */
                alignleft: true, // 
                aligncenter: true, // 
                alignright: true, // 
                /* 2.2.1 */
                // subfield: true, // 
                preview: true, // 
            }
        }
      },
      created(){


        axios.post(restweburl + "header")
            .then((res) => {
                this.header_url = res.data.data;
            })
            .catch(function (error) {
                console.log(error);
            });

            axios.post(restweburl + "info")
            .then((res) => {
                this.name = res.data.data.name;
                // this.label = res.data.data.introduction;
                // this.context=res.data.data.readme;
                // this.school=res.data.data.school+'/'+res.data.data.majority;
            })
            .catch(function (error) {
                console.log(error);
            });

        var params = new URLSearchParams();
        params.append('hwID',this.$route.params.hwID);

        // axios.post(restweburl+'getSavedHomework', params).then(response => {
        //     if(response.data.code==1){
        //     if(response.data.msg=='success'){
        //         this.hwName = response.data.data.name;
        //         this.hwContent = response.data.data.content;
        //     }
        //     else{
        //         this.$message.error(response.data.msg);
        //     }
        //     }
        //     else{
        //         alert("")
        //     }
        // }).catch(error => {
        //     alert(error)
        //     alert("")
        // })


        axios.post(restweburl+'getSavedHomework-stu',params).then(response => {
            if(response.data.code==1){
            if(response.data.msg=='success'){
                if(response.data.data.name!=null){
                    this.hwName = response.data.data.name;
                }
                if(response.data.data.homework!=null){
                    this.hwContent = response.data.data.homework;
                }
                if(response.data.data.content!=null){
                    this.hwWrite = response.data.data.content;
                }
            }
            else{
                this.$message.error(response.data.msg);
            }
            }
            else{
                alert("")
            }
        }).catch(error => {
            alert(error)
            alert("")
        })
      },
      methods:{

        $imgAdd(pos, $file){

            this.upload=true;

            let $vm=this.$refs.md;
            // ..
            var formdata = new FormData();
            formdata.append('file', $file);
            console.log($file);
            axios({
                url: restweburl+'uploadFile',
                method: 'post',
                data: formdata,
            }).then((url) => {
                this.$message({
                    message: '',
                    type: 'success'
                  });
                $vm.$img2Url(pos, url.data.data);
                this.upload=false;
            })
        },
        changeFile() {
            // let $vm=this.$refs.md;
            const fu = document.getElementById("open");
            if (fu == null) return;

            this.upload=true;

            console.log(fu.files[0]);
            // ..
            var formdata = new FormData();
            formdata.append('file', fu.files[0]);
            axios({
                url: restweburl+'uploadFile',
                method: 'post',
                data: formdata,
                // headers: { 'Content-Type': 'multipart/form-data' },
            }).then((url) => {
                // .url![...](0) -> ![...](url)
                /**
                 * $vm mavonEditor
                 * 1. : `import {mavonEditor} from ...` `$vm``mavonEditor`
                 * 2. $refs: htmlref : `<mavon-editor ref=md ></mavon-editor>`$vm` `this.$refs.md`
                 */
                // $vm.$img2Url(url);
                this.$message({
                    message: '',
                    type: 'success'
                  });
                if(this.hwWrite==null)
                {
                    this.hwWrite='['+fu.files[0].name+']'+'('+url.data.data+')';
                }
                else{
                    this.hwWrite=this.hwWrite+'['+fu.files[0].name+']'+'('+url.data.data+')';
                }
                this.upload=false;
            })
        },
        $fileAdd(){
            document.getElementById("open").click();
        },

        save(){
          var params = new URLSearchParams();
          params.append('hwID',this.$route.params.hwID);
          params.append('content',this.hwWrite);

          axios.post(restweburl+'saveHomework-stu', params).then(response => {
              if(response.data.code==1){
                if(response.data.msg=='success'){
                  this.$message({
                    message: '',
                    type: 'success'
                  });
                }
                else{
                  this.$message.error(response.data.msg);
                }
              }
              else{
                  alert("")
              }
          }).catch(error => {
              alert(error)
              alert("")
          })
        },
        submit(){
            var params = new URLSearchParams();
            params.append('hwID',this.$route.params.hwID);
            params.append('content',this.hwWrite);

            axios.post(restweburl+'submitHomework', params).then(response => {
                if(response.data.code==1){
                    if(response.data.msg=='success'){
                    this.$message({
                        message: '',
                        type: 'success'
                    });
                    }
                    else{
                        this.$message.error(response.data.msg);
                    }
                }
                else{
                    alert("")
                }
            }).catch(error => {
                alert(error)
                alert("")
            })
        }
      },
      computed: {
        compiledMarkdown() {
            var rendererMD = new marked.Renderer();
            marked.setOptions({
            renderer: rendererMD,
            highlight: function(code) {
                return hljs.highlightAuto(code).value;
            },
            pedantic: false,
            gfm: true,
            tables: true,
            breaks: true,
            sanitize: true,
            smartLists: true,
            smartypants: true,
            xhtml: true
            });

            return marked.parse(this.hwContent);
        },
      },
    }
</script>
