<template>
    <div>
        <el-header>
            <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
                <el-menu-item v-loading="this.name===''" style="float: right;font-weight: bold;">{{this.name}}CCN</el-menu-item>
            </el-menu>
        </el-header>
        <el-container>
            <el-aside width="200px">

            </el-aside>
            <el-main>
                <router-view></router-view>
            </el-main>
        </el-container>
        <div class="demo-input-suffix" style="font-size: 30px;" >
            {{ this.hwName }}
            <!-- <el-input v-model="hwName" placeholder=""></el-input> -->
        </div>
        <el-divider></el-divider>   
        <div>
            <!--  -->
            <div v-html="compiledMarkdown">
            </div>
        </div>
        <el-divider></el-divider>   
        <div style="font-size: 30px;">
            
        </div>
        <div> 
            <div v-html="compiledMarkdown_2"></div>
            <!-- <mavon-editor ref=md :toolbars="markdownOption" @imgAdd="$imgAdd" v-model="hwWrite">
                <template slot="left-toolbar-after">
                    <button
                    type="button"
                    @click="$fileAdd"
                    class="op-icon el-icon-paperclip"
                    aria-hidden="true"
                    title=""
                    ></button>
                </template>
            </mavon-editor> -->
        </div>
        <div>
            <!-- <el-button @click="save"></el-button>
            <el-button type="primary" @click="submit"></el-button> -->
        </div>

        <div class="block">
  </div>
    </div>
</template>

<script>
    import axios from 'axios'
    import global from './GlobalPage.vue'
    const restweburl = global.ip;

    import { marked } from "marked";

    export default {
        
      data() {
        return {
            hwName:'',
            hwContent:'loading...',
            hwWrite:" ",
            
            markdown: "# loading...",
            html: "",

            markdownOption:{
                bold: true, // 
                italic: true, // 
                header: true, // 
                underline: true, // 
                strikethrough: true, // 
                mark: true, // 
                superscript: true, // 
                subscript: true, // 
                quote: true, // 
                ol: true, // 
                ul: true, // 
                link: true, // 
                imagelink: true, // 
                code: true, // code
                table: true, // 
                // fullscreen: true, // 
                // readmodel: true, // 
                // htmlcode: true, // html
                help: true, // 
                /* 1.3.5 */
                // undo: true, // 
                // redo: true, // 
                // trash: true, // 
                // save: true, // eventssave
                /* 1.4.2 */
                navigation: true, // 
                /* 2.1.8 */
                alignleft: true, // 
                aligncenter: true, // 
                alignright: true, // 
                /* 2.2.1 */
                // subfield: true, // 
                preview: true, // 
            },
            name:''
        }
      },
      created(){
        axios.post(restweburl + "header")
            .then((res) => {
                this.header_url = res.data.data;
            })
            .catch(function (error) {
                console.log(error);
            });

            axios.post(restweburl + "info")
            .then((res) => {
                this.name = res.data.data.name;
                // this.label = res.data.data.introduction;
                // this.context=res.data.data.readme;
                // this.school=res.data.data.school+'/'+res.data.data.majority;
            })
            .catch(function (error) {
                console.log(error);
            });



        var params = new URLSearchParams();
        params.append('hwID',this.$route.params.hwID);

        axios.post(restweburl+'getSavedHomework-stu',params).then(response => {
            if(response.data.code==1){
            if(response.data.msg=='success'){
                this.hwName = response.data.data.name;
                this.hwContent = response.data.data.homework;
                this.hwWrite = response.data.data.content;
            }
            else{
                this.$message.error(response.data.msg);
            }
            }
            else{
                alert("")
            }
        }).catch(error => {
            alert(error)
            alert("")
        })
      },

      computed: {
        compiledMarkdown() {
            return marked.parse(this.hwContent);
        },
        compiledMarkdown_2() {
            return marked.parse(this.hwWrite);
        },
      },
    }
</script>
